header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 1rem;
  font-size: 1.2rem;
  padding-bottom: 1rem;
  background-color: rgb(108, 197, 248);
  position: relative;
}

header input {
  height: 45px;
  width: 45px;
}

.logo-container {
  flex: 4;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  gap: 10px;
}

.logo-container img {
  height: 60px;
  width: 60px;
}

.menu-container {
  flex: 1;
}

.burger {
  z-index: 100;
  padding-top: 5px;
  width: 35px;
  height: 35px;
  margin: auto;
  display: block;
}

.menu {
  position: fixed;
  top: 0%;
  left: 0%;
  right: 0px;
  bottom: 0px;
  transform: translateX(200%);
  transition: 0.2s;
  width: 100%;
  height: 100%;
  text-align: left;
  list-style: none;
  background-color: rgb(174, 220, 247);
  font-size: 2rem;
  padding-top: 30%;
  padding-left: 20px;
  line-height: 2;
  z-index: 99;
}
.menu a {
  color: black;

}

.menu a:hover {
  color: white;
  text-decoration: underline;
}

.show {
  transform: translateX(0%);
}
